import React from 'react';
import Board from './Board';
import Status from './Status';

const initialGameState = {
  squares: Array(9).fill(null),
  gameIndex: 0,
  gameStatus: 'X\'s turn'
}
class TicTacToe extends React.Component {

  constructor(){
    super()
    const {gameIndex, gameStatus, squares} = initialGameState
    this.state = {
      gameIndex, gameStatus, squares: [...squares]
    }
  }
  getPlayer() {
    return this.state.gameIndex % 2 === 0? 'X' : 'O';
  }
  updateSquares(squareIndex){
    let {squares, gameIndex} = this.state;
    
    if(!squares[squareIndex]) {
      squares[squareIndex] = this.getPlayer();
      gameIndex++;
      this.setState({squares, gameIndex}, () => this.updateStatus())
    }
  }

  updateStatus(){
    let {gameIndex, gameStatus} = this.state;
    
    if(gameIndex === 9){
      gameStatus = 'Game has ended'
    }else {
      gameStatus = this.getPlayer() + '\'s turn'
    }
    this.setState({gameStatus})
  }

  restartGame() {
    const {gameIndex, gameStatus, squares} = initialGameState
    this.setState({gameIndex, gameStatus, squares: [...squares] })
  }

  renderButton() {
    return <a className="restart-button" onClick={()=>this.restartGame()}>Start a new one?</a>
  }
  render() {
    const {squares, gameStatus, gameIndex} = this.state;
    return (
      <div>
        <Status gameStatus={gameStatus} gameIndex={gameIndex} restartGame={()=>this.restartGame()}/>
        <Board squares={squares} updateSquares={(index)=>this.updateSquares(index)}/>
        {gameIndex ===9 && 
          <div className="restart-button-parent">
            {gameStatus}!
            {gameIndex === 9 && this.renderButton()}
          </div>
        }
        
      </div>
    );
  }
}

export default TicTacToe;



