import React from 'react';

class Square extends React.Component {
  render() {
    return (
      <div className={"square " + (this.props.value === 'X' ? "x-element" : "o-element")} onClick={()=>this.props.updateSquares()}>
        {this.props.value}
      </div>
    );
  }
}

export default Square;



