import React from 'react';

class Status extends React.Component {
  renderButton() {
    return <button className="restart-button" onClick={()=>this.props.restartGame()}>Restart Game</button>
  }
  renderStatus(){
    let {gameStatus, gameIndex } = this.props
    let str1 = gameStatus.substring(0, 1)
    let str2 = gameStatus.substring(1, gameStatus.length)
    return (
      <React.Fragment>
        <span className={this.props.gameIndex === 9 ? "" : (gameIndex % 2 === 0 ? "x-element" : "o-element")}>{str1}</span>
        <span>{str2}</span>
      </React.Fragment>
    )
  }
  render() {
    return (
      <div className="game-status">
        <div>
          {this.renderStatus()}
        </div>
        {/* <div>
          {this.props.gameIndex ===9 && this.renderButton()}
        </div> */}
      </div>
    );
  }
}

export default Status;



